/* src/ChatWindow.css */
body, html, #root, .App {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.App {
  display: flex;
  justify-content: flex-end;
  align-items: stretch;
  height: 100%;
}

.chat-window {
    width: 88%;
    height: 100%;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
  }
  
  .messages {
    flex: 1;
    padding: 10px;
    overflow-y: auto;
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center; /* Center the messages horizontally */
    margin: 0 auto; /* Center the messages container */
  }
  
  .message {
    padding: 10px;
    margin: 5px 0;
    border-radius: 5px;
    max-width: 70%;
  }
  
  .message.me {
    align-self: flex-end;
    background-color: #daf1da;
  }
  
  .message.bot {
    align-self: flex-start;
    background-color: #f1f1f1;
  }
  
  .input-area {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border-top: 1px solid #ccc;
    width: 50%
  }
  
  .input {
    background-color: #f5f5f5;
    color: #242424;
    padding: .15rem .5rem;
    min-height: 40px;
    border-radius: 4px;
    outline: none;
    border: none;
    line-height: 1.15;
    box-shadow: 0px 10px 20px -18px;
    width: 50%;
  }
  
  .input:focus {
    border-bottom: 2px solid #5b5fc7;
    border-radius: 4px 4px 2px 2px;
  }
  
  .input:hover {
    outline: 1px solid lightgrey;
  }
  
  .input-area button {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #4caf50;
    color: white;
    cursor: pointer;
  }
  
  .input-area button:hover {
    background-color: #45a049;
  }

  .select-model {
    padding-top: 10px;
    padding-left: 20px;
  }